import React from 'react'
import '../styles/Login.css';

function Login() {
    return (
        <div>
        <form>
            <div className="admin-container">
                <input type="text" placeholder="Wpisz login..." />
                <input type="password" placeholder="Wpisz hasło..." />
            </div>
        </form>
        </div>
    )
}

export default Login
